import React from "react";
import { Form } from "react-bootstrap";
import { css } from "aphrodite";

const TableLabel = ({ data, userDefinedControlId, styles2, styleSheet2, onDoubleClick = () => {} }) => {
  return (
    <Form.Label
      control_id={data.id}
      elem_type="table"
      className={[
        styles2.tableFormLabel,
        "tableTitleDefaultStyle",
        "controlFontSize",
        " spacingThemeLabel ",
        css(styleSheet2.styling)
      ].join(" ")}
      frameindex={data.frameIndex}
      elementindex={data.elementIndex}
      onDoubleClick={onDoubleClick}
    >
      {data?.style?.Label}
    </Form.Label>
  );
};

export default TableLabel;
