function getFlexValue(column) {
  let flexValue = column?.style?.ColumnWidth;
  let newFlexValue = flexValue.replace("%", "");
  let intValue = parseInt(newFlexValue);
  let floatVal = intValue / 10;
  return floatVal;
}

function getWidthValue(column) {
  let flexValue = column?.style?.ColumnWidth;
  let newFlexValue = flexValue.replace("%", "");
  let intValue = parseInt(newFlexValue);
  return intValue;
}

function checkMaskingType(tableData, key) {
  for (let item of tableData.columnList) {
    if (item.style.complexFieldName === key || item.style.ColumnName === key) {
      return item.style?.ColumnMasking || "";
    }
  }
}

function maskingExists(value, maskingList) {
  for (let item of maskingList) {
    if (typeof value !== "boolean" && value.includes(item)) {
      return true;
    }
  }
  return false;
}

function formatPhoneNumber(phoneNumber) {
  if (phoneNumber?.includes("(")) return phoneNumber;
  const areaCode = phoneNumber.slice(0, 2);
  const remainingDigits = phoneNumber.slice(2);
  let result = `(${areaCode}) ${remainingDigits}`;
  return result;
}

function formatUS(amount, decimalCount = 0, decimal = ".", thousands = ",") {
  try {
    let formattedAmount = amount.replaceAll(",", "");
    decimalCount = Math.abs(decimalCount);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

    const negativeSign = formattedAmount < 0 ? "-" : "";

    let i = parseInt((formattedAmount = Math.abs(Number(formattedAmount) || 0).toFixed(decimalCount))).toString();
    let j = i.length > 3 ? i.length % 3 : 0;

    return (
      negativeSign +
      (j ? i.substr(0, j) + thousands : "") +
      i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) +
      (decimalCount
        ? decimal +
          Math.abs(formattedAmount - i)
            .toFixed(decimalCount)
            .slice(2)
        : "")
    );
  } catch (e) {
    console.log(e);
  }
}

function formatIndia(val) {
  if (val?.includes(",")) return val;
  let lastThree = val.substring(val.length - 3);
  let otherNumbers = val.substring(0, val.length - 3);
  if (otherNumbers != "") lastThree = "," + lastThree;
  let res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
  return res;
}

function formatValue(maskingType, value) {
  let prefix = "";
  let thousandSeparator = "";
  let suffix = "";
  let thousandsGroupStyle = "";
  let format = "";
  let isPassword = "";
  let maskingList = ["B$ ", "$ ", " ", " ", "", " ", " CHF", "%", "(", "-"];
  if (maskingType) {
    if (maskingType === "currency_bahamas") {
      prefix = "B$ ";
      thousandSeparator = ",";
    }
    if (maskingType === "currency_dollar") {
      prefix = "$ ";
      thousandSeparator = ",";
    }
    if (maskingType === "currency_euro") {
      prefix = " ";
      thousandSeparator = ",";
    }
    if (maskingType === "currency_naira") {
      prefix = " ";
      thousandSeparator = ",";
    }
    if (maskingType === "currency_rupees") {
      prefix = " ";
      thousandSeparator = ",";
    }
    if (maskingType === "currency_yen") {
      prefix = " ";
      thousandSeparator = ",";
    } else if (maskingType === "currency_french") {
      suffix = " CHF";
      thousandSeparator = ",";
    } else if (maskingType === "percentage") {
      suffix = "%";
    } else if (maskingType === "dgroup2") {
      thousandSeparator = ",";
      thousandsGroupStyle = "lakh";
      value = formatIndia(value);
    } else if (maskingType === "dgroup3") {
      thousandSeparator = ",";
      value = formatUS(value);
    } else if (maskingType === "(00) 0000000000") {
      format = "(##) ##########";
      value = formatPhoneNumber(value);
    } else if (maskingType === "000-000") {
      format = "###-###";
      value = value.replace(/(\d{3})(\d{3})/, "$1-$2");
    } else if (maskingType === "password") {
      isPassword = "password";
    }
  }
  if (value && maskingExists(value, maskingList)) {
    return value;
  }
  let finalValue = prefix + " " + value + " " + suffix;
  return finalValue;
}

function isDropDown(tableData, columnName, value, extraOptions) {
  for (let column of tableData.columnList) {
    if (columnName === column.style.ColumnName || columnName === column.style.complexFieldName) {
      if (column.style.ColumnType === "combo" && tableData.elem_type !== "table") {
        let eleJson = recursiveJsonReader1(
          tableData.listviewFrameLayoutJson,
          tableData.elem_type === "listview"
            ? `${tableData.id}_${columnName}`
            : tableData?.elem_type?.toLowerCase() === "advancelistview"
            ? column.style.complexFieldName
            : columnName,
          "control"
        );
        let opt = cloneDeep(eleJson?.options || eleJson?.style?.options || null || null);
        if (extraOptions) {
          opt?.push(...extraOptions);
        }
        console.log("foundItem..", eleJson);
        if (opt) {
          for (let option of opt) {
            if (value === option.value) {
              return option.label;
            }
          }
        }
        return value || "";
      } else {
        return value;
      }
    }
  }
  return value;
}

export function formatTableData(tableData, formattedData) {
  let resultData = [];
  const rowData = tableData.rowData;
  const response1 = tableData?.rowData ? tableData : {};
  const response2 = response1 ? response1?.rowData : [];

  const rowDataLength = response2.length;
  const MaxLoopIteration = 999999;
  // let isDisable = false;

  if (rowDataLength < MaxLoopIteration) {
    for (let row = 0; row < rowDataLength; row++) {
      let obj = { id: parseInt(row) };
      obj["disabled_column_names"] = [];
      obj["cellBackGround"] = new Map();
      obj["row_disabled"] = false;
      obj["row_visible"] = true;

      if (rowData[row]?.Disabled) {
        obj["row_disabled"] = true;
      }
      if (rowData[row]?.Visible === false) {
        obj["row_visible"] = false;
        continue;
      }

      Object.entries(rowData[row]).forEach((entry, index) => {
        let valueObj = entry[1];
        let key = valueObj.name;
        let value = isDropDown(tableData, valueObj.name, valueObj.value, valueObj.extraOptions);
        if (tableData.elem_type === "advancelistView" || tableData.elem_type === "listview") {
          if (value) {
            let maskingType = checkMaskingType(tableData, key);
            let finalValue = formatValue(maskingType, value);
            obj[key] = finalValue;
          } else {
            obj[key] = value;
          }
        } else {
          obj[key] = value;
        }

        if (valueObj?.style?.Disabled) {
          // obj["disabled"] = valueObj?.style?.Disabled
          obj["disabled_column_names"].push(key);
          // isDisabled = true;
        }
        if (valueObj?.style?.BackColor) {
          obj["cellBackGround"].set(key, valueObj?.style?.BackColor);
        }
        if (valueObj?.clearCombo) {
          obj[`${key}_clearOptions`] = valueObj.clearCombo;
        }
        if (valueObj?.clearComboNew) {
          obj[`${key}_clearOptionsNew`] = valueObj.clearComboNew;
        }
        if (valueObj?.extraOptions) {
          obj[`${key}_extraOptions`] = valueObj.extraOptions;
          obj[`${key}_newOptions`] = valueObj.extraOptions;
        }
        if (valueObj?.removeOptionIndex || valueObj?.removeOptionIndex === 0) {
          obj[`${key}_removeOptionIndex`] = valueObj.removeOptionIndex;
        }
      });

      resultData.push(obj);

      if (
        row === rowData?.length - 1 &&
        (tableData?.columnList?.some((item) => item?.style?.ShowTotalListview === "true") ||
          tableData?.style?.ShowTotal === "true")
      ) {
        const showTotalRow = formattedData.filter((item) => item.isShowTotal);
        const filteredResultData = resultData.filter((item) => !item.isShowTotal);
        const showTotalEnabledColumn = { isShowTotal: true, row_disabled: false };

        if (showTotalRow?.length) {
          filteredResultData.push(showTotalRow[0]);
        } else {
          tableData?.columnList?.forEach((item) => {
            if (item?.style?.ShowTotalListview === "true")
              showTotalEnabledColumn[
                tableData.elem_type.toLowerCase() === "advancelistview"
                  ? item?.style?.complexFieldName
                  : item?.style?.ColumnName
              ] = "";
          });

          filteredResultData.push(showTotalEnabledColumn);
        }

        resultData = [...filteredResultData];
      }
    }
  }
  return resultData;
}

function findTextarea(tableData) {
  let maxHeight = 75; // textarea should minimum have 3 rowHeight for better UI (1 rowHeight = 25px)
  let isTextarea = false;
  for (let column of tableData.columnList) {
    if (column.style.ColumnType === "textarea") {
      const columnHeight = column?.style?.TableTextAreaHeight;
      isTextarea = true;

      if (columnHeight > maxHeight) {
        maxHeight = columnHeight;
      }
    }
  }

  return { maxHeight, isTextarea };
}

const reformatRegex = /[^-0-9.]/g;

const parseNum = (num) => (isNaN(parseFloat(num)) ? 0 : parseFloat(num));

const totalAllowedForMasking = [
  "currency_bahamas",
  "currency_dollar",
  "currency_euro",
  "currency_naira",
  "currency_rupees",
  "currency_yen",
  "currency_french",
  "dgroup2",
  "dgroup3"
];

export const getTotal = (dataToSum = [], key, tableData) => {
  let sum = "0";
  const curMasking = checkMaskingType(tableData, key);
  const isTotalAllowed = totalAllowedForMasking.includes(curMasking);

  if (!isTotalAllowed && curMasking && curMasking !== "nomasking") {
    return "";
  }
  let curColumn = [];
  if (tableData?.elem_type?.toLowerCase() === "advancelistview") {
    curColumn = tableData.columnList.filter((item) => key === item.style?.complexFieldName);
  } else {
    curColumn = tableData.columnList.filter((item) => key === item?.style?.ColumnName);
  }

  if (curColumn && curColumn.length && (!tableData?.style?.ShowTotal || tableData?.style?.ShowTotal === "false")) {
    const isShowTotalCheck = curColumn[0]?.style?.ShowTotalListview;
    if (!isShowTotalCheck || isShowTotalCheck === "false") {
      return "";
    }
  }

  for (let i = 0; i < dataToSum?.length - 1; i++) {
    if (typeof dataToSum?.[i]?.[key] === "string") {
      let isNanCheck = false;
      let numProp = dataToSum[i][key].replace(reformatRegex, ""); // remove all char except number, minus, dot

      if (!isTotalAllowed) {
        isNanCheck = isNaN(dataToSum[i][key]);
      }

      const num = new Big(numProp === "" || isNanCheck ? "0" : numProp);

      // const num = dataToSum[i][key].replace(reformatRegex, "");
      let summedValue = num.plus(sum);
      // sum += parseNum(num);
      let columnObj = [];
      if (tableData.elem_type.toLowerCase() === "advancelistview") {
        columnObj = tableData.columnList.filter((item) => key === item?.style?.complexFieldName);
      } else {
        columnObj = tableData.columnList.filter((item) => key === item?.style?.ColumnName);
      }

      if (columnObj[0]?.style["complexFieldVariablePrecision"]) {
        sum = summedValue.toFixed(parseInt(columnObj[0]?.style["complexFieldVariablePrecision"]));
      } else if (columnObj[0]?.style["complexFieldVariablePrecision"] === "") {
        sum = summedValue.toFixed(2);
      } else {
        sum = summedValue.toFixed(0);
      }
    }
  }
  // let numStr = "1.0"
  // if(typeof sum === "string"){
  //   sum = sum.replace(/0+$/, '');
  //   if (sum.endsWith('.')) {
  //     sum += '0';
  //   }
  // }
  return sum;
};

