import React, { useState } from "react";
import { Button } from "react-bootstrap";
import { useSelector, useDispatch, updateContexts } from "../../../../Redux";
import { makeStyles } from "@material-ui/core";
import styles2 from "../../../../UI/css/COLUMN.module.css";
import AddRecordIcon from "../../../../Icons/AddRecordIcon";
import "./DataAdditionAboveSection.css";
import ELEMENT from "../../../../UI/BasicFormControls/ELEMENT";
import {
  getGdToken,
  getWIToken,
  updatedComponentInFormStates,
} from "../../IFormDesignerLogic";
import axios from "../../../../axios";
import { changeLayoutOfElements } from "../../SectionLayoutLogic";
import { updateGridContexts } from "../../../../Redux/Actions/FormStateActions";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";

const useStyles = makeStyles((theme) => ({
  gridButtons: {
    "& .secondaryBtn": {
      color: (prop) =>
        prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
          ? `${prop.SecondaryButtonFontColor} !important`
          : "#0072c6 !important",
      backgroundColor: (prop) => "#fff !important",
      // borderColor: (prop) => prop.primaryBackgroundColor,
      border: (prop) =>
        prop.SecondaryButtonBorderWidth && prop.SecondaryButtonBorderWidth !== "undefined" && prop.secondaryBorderColor
          ? `${prop?.SecondaryButtonBorderWidth || "1px"} solid ${prop.secondaryBorderColor}`
          : "1px solid #0072c6 !important",
      "&:focus": {
        boxShadow: (prop) => `0 0 0 0.2rem ${prop.primaryBackgroundColor}`,
        color: (prop) =>
          prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
            ? `${prop.SecondaryButtonFontColor} !important`
            : "#0072c6 !important",
        backgroundColor: (prop) => "#fff !important",
        // borderColor: (prop) => prop.primaryBackgroundColor,
        border: (prop) =>
          prop.SecondaryButtonBorderWidth &&
          prop.SecondaryButtonBorderWidth !== "undefined" &&
          prop.secondaryBorderColor
            ? `${prop?.SecondaryButtonBorderWidth || "1px"} solid ${prop.secondaryBorderColor}`
            : "1px solid #0072c6 !important"
      },
      "& svg line": {
        stroke: (prop) =>
          prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
            ? `${prop.SecondaryButtonFontColor} !important`
            : "#0072c6 !important"
      }
    }
    // "& .secondaryBtn": {
    //   color: (prop) => prop.secondaryColor,
    //   borderColor: (prop) => prop.secondaryBorderColor
    // }
  },
  tooltip: {
    maxWidth: "160px",
    // maxHeight: "96px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12
    // marginTop: "0px",
  },
  arrow: {
    "&:before": {
      border: "1px solid #70707075"
    },
    color: "white",
    fontSize: 15
  }
}));

export const DataAdditionAboveSection = (props) => {
  const { data, previewmode, containerId } = props;
  const translationStyles1 =
    window?.appformBuilder_configs?.selectedLanguage === "ar" ||
    window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

  const contexts = GetStateFromData(props.containerId);
  const gridContexts = GetStateFromData(props.containerId,"data2");  

  const {
    advanceListviewStateJson,
    selectedRowIndex,
  } = gridContexts.gridStates;

  const {
    updateJson,
    // changeSectionLayout,
    formState2,
    modeReadOrWrite,
    // setListViewAddRecordFrom,
    // advanceListviewStateJson,
    listviewColumnInfo,
    // setListviewClearState,
    setRecordCount,
    recordCount,
    showAlertConfirmNotifications,
    listviewModifyMode,
    setListviewModifyMode,
    // selectedRowIndex,
    // setSelectedRowIndex,
  } = contexts.contexts;

  const dispatch = useDispatch();

  const { updatedComponentInFormState } = updateJson;
  let isEnable = true;
  if (previewmode && modeReadOrWrite === "r") {
    data.style.Enable = "false";
    isEnable = false;
  }
  const themeData = formState2.theme;
  const controlThemeData = themeData?.["ButtonStyle"];

  const addRecordButtonStyle = useStyles({
    primaryColor: `${controlThemeData["PrimaryButtonFontColor"] || "#fff"} !important`,
    primaryBackgroundColor:
      `${controlThemeData["PrimaryButtonBackgroundColor"]}  !important` || `var(--button_color)"} !important`,
    primaryBorderColor: `${controlThemeData["PrimaryButtonBorderRadius"] || "var(--button_color)"} !important`,
    secondaryColor: `${controlThemeData["SecondaryButtonFontColor"] || "var(--button_color)"} !important`,
    secondaryBorderColor: `${controlThemeData["SecondaryButtonBorderColor"] || "var(--button_color)"} !important`,
    SecondaryButtonBorderWidth: `${controlThemeData["SecondaryButtonBorderWidth"]}`,
    SecondaryButtonFontColor: `${controlThemeData["SecondaryButtonFontColor"]}`,
    SecondaryButtonFontFamily: `${controlThemeData["SecondaryButtonFontFamily"]}`
  });

  function handleCol(e) {
    let colValue = e.target.value;
    data.listviewFrameLayoutJson.children[0].children[0].ColumnLayout =
      colValue;
      changeLayoutOfElements(
        data.listviewFrameLayoutJson.children[0].children[0],
        colValue,
        "listview",true
      );
    updatedComponentInFormState(data.id, data);
  }

  function checkColumnType(columnList, columnName) {
    if (columnList) {
      for (let column of columnList) {
        if (column.style.ColumnName === columnName) {
          return column.style.complexFieldType;
        }
      }
    }
  }

  function addRecord() {
    let result = true;
    let customValidationResult = true;

    if (window.tableOperation) {
      // tableId,operationType
      result = window.tableOperation(data.id, "AddRow");
    }
    if (window.customListViewValidation) {
      customValidationResult = window.customListViewValidation(data.id, "A");
    }

    let dataContent = {};
    const tableId = data?.id?.split("_")?.[0];
    let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
    if(currentListviewColumnInfo){
      for (let [key, value] of currentListviewColumnInfo) {
        let colType = checkColumnType(
          props?.data?.columnList,
          key
        );
        let reformatRegex = /[*]/g;
        if (colType === "Float") {
          reformatRegex = /[^0-9.]/g;
        } else if (colType === "Integer" || colType === "Long") {
          reformatRegex = /[^0-9]/g;
        }
        if (typeof value === "string") {
          dataContent[key] = value.replace(reformatRegex, "");
        } else {
          dataContent[key] = value;
        }
      }
    }
    

    if (result && customValidationResult) {
      let jsonToSend = {
        witoken: getWIToken(props.containerId), // getWIToken()
        controlId: data.id,
        gdtoken: getGdToken(props.containerId), //getGdToken()
        dataValue: dataContent,
      };
      let remainingURL = "/tableAction/addRow";

      axios
        .post(remainingURL, jsonToSend, { withCredentials: true })
        .then((response) => {
          if (response && response.data && response.data.status === "0") {
            if (!data.rowData) {
              data.rowData = [];
            }
            let rowData = {}; //response.data.valueJson;
            Object.entries(response.data.valueJson).map((entry, index) => {
              let valueObj = entry[1];
              let itemValue = "";
              const tableId = data?.id?.split("_")?.[0];
              let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
              if(currentListviewColumnInfo){
                for (let [key, value] of currentListviewColumnInfo) {
                  if (valueObj.name === key) {
                    itemValue = value + "";
                  }
                }
              }
              valueObj.value = itemValue;
            });
            rowData = response.data.valueJson;
            data.rowData.push(rowData);
            listviewColumnInfo.clear();
            if (advanceListviewStateJson) {
              updatedComponentInFormState(data.id, data, true);
            } else {
              updatedComponentInFormState(data.id, data);
            }
            dispatch(updateGridContexts({name: "listViewAddRecordFrom", key: props.containerId, value: false}));
            dispatch(updateGridContexts({key: props.containerId, name: "listviewClearState", value: true}));
            updatedComponentInFormState(data.id, data);
            setRecordCount(recordCount + 1);
          } else if (
            response &&
            response.data &&
            response.data.status === "-1"
          ) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "DUPLICATE_ROW_MESSAGE",
            });
          } else if (
            response &&
            response?.data &&
            response.data.status === "-2"
          ) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "INVALID_DATA_MESSAGE",
            });
          }
        });
    }
    document.activeElement.blur();
  }

  function modifyRowHandler() {
    let dataContent = {};
    dispatch(updateGridContexts({name: "listViewAddRecordFrom", key: props.containerId, value: false}));
    const tableId = data?.id?.split("_")?.[0];
    let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
    if(currentListviewColumnInfo){
      for (let [key, value] of currentListviewColumnInfo) {
        let colType = checkColumnType(
          props?.data?.columnList,
          key
        );
        let reformatRegex = /[*]/g;
        if (colType === "Float") {
          reformatRegex = /[^0-9.]/g;
        } else if (colType === "Integer" || colType === "Long") {
          reformatRegex = /[^0-9]/g;
        }
        if (typeof value === "string") {
          dataContent[key] = value.replace(reformatRegex, "");
        } else {
          dataContent[key] = value;
        }
      }
    }


    let customValidationResult = true;
    if (window.customListViewValidation) {
      customValidationResult = window.customListViewValidation(data.id, "M");
    }

    if (customValidationResult) {
      let jsonToSend = {
        witoken: getWIToken(containerId), // getWIToken()
        controlId: data.id,
        gdtoken: getGdToken(containerId), //getGdToken()
        dataValue: dataContent,
        rowIndex: selectedRowIndex,
      };

      let remainingURL = "/tableAction/modifyRowInListView";
      const extraRowData = window?.currentRowData;
      axios
        .post(remainingURL, jsonToSend, { withCredentials: true })
        .then((response) => {
          let rowData = {};
          Object.entries(response.data.valueJson).map((entry, index) => {
            let valueObj = entry[1];
            let itemValue = "";
            const tableId = data?.id?.split("_")?.[0];
            let currentListviewColumnInfo = listviewColumnInfo.get(tableId);
            if(currentListviewColumnInfo){
              for (let [key, value] of currentListviewColumnInfo) {
                if (valueObj.name === key) {
                  itemValue = value + "";
                }
              }
            }
            valueObj.value = itemValue;
          });
          rowData = response.data.valueJson;
          if (response && response.data && response.data.status === "0") {
            if (extraRowData && extraRowData.id === selectedRowIndex) {
              for (let key in rowData) {
                if (
                  extraRowData.hasOwnProperty(
                    `${rowData[key].name}_extraOptions`
                  )
                ) {
                  rowData[key].extraOptions =
                    extraRowData[`${rowData[key].name}_extraOptions`];
                }
                if (
                  extraRowData.hasOwnProperty(
                    `${rowData[key].name}_clearOptions`
                  )
                ) {
                  rowData[key].clearCombo =
                    extraRowData[`${rowData[key].name}_clearOptions`];
                }
                if (
                  extraRowData.hasOwnProperty(
                    `${rowData[key].name}_removeOptionIndex`
                  )
                ) {
                  rowData[key].removeOptionIndex =
                    extraRowData[`${rowData[key].name}_removeOptionIndex`];
                }
              }
            }

            data.rowData[selectedRowIndex] = rowData;

            listviewColumnInfo.clear();

            if (advanceListviewStateJson) {
              updatedComponentInFormState(data.id, data, true);
            } else {
              updatedComponentInFormState(data.id, data);
            }

            dispatch(updateGridContexts({key: props.containerId, name: "listviewClearState", value: true}));
            dispatch(updateGridContexts({key: props.containerId, name: "selectedRowIndex", value: -1}));;
            updatedComponentInFormState(data.id, data);
            setRecordCount(recordCount + 1);
            setListviewModifyMode(false)

            if (window.modifyRowPostHook) {
              window.modifyRowPostHook(
                props.data.id,
                selectedRowIndex
              );
            }
          } else if (
            response &&
            response.data &&
            response.data.status === "-1"
          ) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "DUPLICATE_ROW_MESSAGE",
            });
          } else if (
            response &&
            response.data &&
            response.data.status === "-2"
          ) {
            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: "INVALID_DATA_MESSAGE",
            });
          }
        });
    }
    document.activeElement.blur();
  }

  return (
    <div id="container_aboveSection">
      <div id="header_aboveSection">
        <div id="left">
          <span>Data Addition</span>
        </div>
        <div id="right" className={[styles2.tableSearchAndSelectInnerDiv, addRecordButtonStyle.gridButtons].join(" ")}>
          {previewmode ? (
            <></>
          ) : (
            <>
              <span>COL</span>
              <select
                onChange={(e) => handleCol(e)}
                value={
                  data.listviewFrameLayoutJson.children[0].children[0]
                    .ColumnLayout
                }
              >
                {/* <option>--</option> */}
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="6">6</option>
                <option value="12">12</option>
              </select>
            </>
          )}

          {(data?.style?.["HideAdd"] === "false" ||
            data?.style?.["HideAdd"] === false) &&
          data?.style?.["Enable"] === "true" &&
          previewmode ? (
            <>
              {listviewModifyMode ? (
                <Button
                disableFocusRipple
                disableRipple
                tabIndex={0}
                  size="sm"
                  id={`${data.id}_modify_record_btn`}
                  className={`${styles2.addRecordButton} ${
                    translationStyles1 ? styles2.addRecordButton_mr : styles2.addRecordButton_ml
                  } button primary secondaryBtn formBuilder-MuiButton-outlined`}
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                  style={{
                    pointerEvents: isEnable === false ? "none" : "initial",
                  }}
                  onClick={modifyRowHandler}
                  title="Modify"
                  disabled={data.columnList.length > 0 ? false : true}
                >
                  {/* <span className={styles2.plusIconSpan}>
                    <AddRecordIcon />
                  </span> */}
                  {"Modify"}
                </Button>
              ) : (
                <Button
                disableFocusRipple
                disableRipple
                tabIndex={0}
                  size="sm"
                  id={`${data.id}_add_record_btn`}
                  className={`${styles2.addRecordButton} ${
                    translationStyles1 ? styles2.addRecordButton_mr : styles2.addRecordButton_ml
                  } button primary secondaryBtn formBuilder-MuiButton-outlined`}
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                  style={{
                    pointerEvents: isEnable === false ? "none" : "initial",
                  }}
                  onClick={addRecord}
                  title="Add Record"
                  disabled={getGdToken() ? false : true}
                >
                  <span className={styles2.plusIconSpan}>
                    <AddRecordIcon />
                  </span>
                  {"Add Record"}
                </Button>
              )}
            </>
          ) : (
            <></>
          )}
        </div>
      </div>
      <div id="controls">
        <ELEMENT
          containerId={containerId}
          {...props}
          // data={props.formdata}
          //  data={props.data}

          data={data.listviewFrameLayoutJson}
        />
      </div>
    </div>
  );
};
