import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles2 from "../css/COLUMN.module.css";
import { StyleSheet } from "aphrodite";
import { CSSPreparation1 } from "../../components/Properties/CSSPreparation1.jsx";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { useDispatch } from "../../Redux";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import CustomizedAlert from "../../ReusableComponents/Alert/CustomizedAlert";
import { handleDataAdditionNew, handleDisplayProperty, updatePartialJSON } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData } from "../../components/CustomHooks/CustomHooks";
import ActionHeader from "./TableComponent/ActionHeader";
import DataTable from "./DataTable";
import { DataAdditionAboveSection } from "../../components/IFormDesignerLayout/IFormDesignerComponents/ListviewProperties/DataAdditionAboveSection";
import { updateContextsStates, updateGridContexts } from "../../Redux/Actions/FormStateActions.js";
import ListviewPopUp from "../../components/IFormDesignerLayout/IFormDesignerComponents/ListviewPopUp.jsx";

const LISTVIEW = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const gridContexts = GetStateFromData(props.containerId,"data2");
  const { tempCanWidth, controlSelected, renderListView }  = GetState("tempCanWidth", "controlSelected","renderListView");

  const {
    advanceListviewStateJson,
    openCanvasForPreview,
    subFormListviewJson,
    openCanvasForPreviewAdvance,
    selectedTableRow
  } = gridContexts.gridStates;

  const {
    IdPrecedence,
    deleteControl,
    formState2,
    device,
    setSelectedControl,
    dataObjectsContent,
    openQuickToolFor
  } = contexts.contexts;
  const conditionalQuickToolOpening = props.id === openQuickToolFor ? true : false;

  const isSubformInPage = props?.isSubformInPage || false;
  const dispatch = useDispatch();
  const data = props.data;
  const actualControlId = props.data.id;

  let mappingId = props.data["style"] && props.data["style"]["MappingId"];
  let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
  let userDefinedControlId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  useEffect(() => {
    if (props?.data?.isAdvanceListview && props?.data?.style?.Searching === "true") {
      props.data.style.Searching = "false";
      updatePartialJSON(props.data.id,props.data);
    }
  }, []);

  const [selectedRowsList, setSelectedRowsList] = useState([]);
  const [latestAddedRow, setLatestAddedRow] = useState(-1);
  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [sortableColumns, setSortableColumns] = useState("");
  const [isSearched, setIsSearched] = useState(false);
  const [recordCount, setRecordCount] = useState(0);
  const [tableData, setTableData] = useState(props.data || {});
  const [loading, setLoading] = useState(false);
  const [rowData, setRowData] = useState([]);
  const [filterData, setFilterData] = useState([]);
  const [checkedKeys, setCheckedKeys] = useState([]);

  const controlThemeData = formState2.theme?.["TableStyle"];
  const themeData = formState2.theme;

  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const [searchValue, setSearchValue] = useState("");

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => {
    if (props?.previewmode) {
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
    }
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth
  ]);

  useEffect(() => {
    setTableData(props.data);
  }, [props.data?.style?.updateFormState, JSON.stringify(props?.data?.rowData)]);

  useEffect(() => {
    if (!props.previewmode && props?.data?.columnList?.length === props?.data?.initialColumnLength) {
      if (!props.data.listviewFrameLayoutJson) {
        props.data.listviewFrameLayoutJson = {};
        handleDataAdditionNew(undefined, props.data, dataObjectsContent);
      } else {
        handleDataAdditionNew(undefined, props.data, dataObjectsContent);
      }
    }
  }, [props?.data?.columnList?.length]);

  useEffect(() => {
    if(props.previewmode) {
      setselectedTableRowfunction(rowData[checkedKeys[0]], props.data.id);
    }
  }, [rowData, checkedKeys]);

  function setselectedTableRowfunction(value, key){
    var temp_selectedTableRow = { ...selectedTableRow };
    temp_selectedTableRow[key] = value;

    dispatch(updateGridContexts({ name: "selectedTableRow", key: props.containerId, value: temp_selectedTableRow, renderAdvListView: false, renderListView: true }));
  }

  var tableTitleDefautStyle = {
    "font-family": "Open Sans",
    "font-weight": "Bold",
    "font-style": "normal",
    color: "#000000"
  };

  if (themeData && themeData["BasicTheme"] && themeData["BasicTheme"]["FontColor"]) {
    tableTitleDefautStyle["font-family"] = themeData["BasicTheme"]["FontFamily"];
  }
  if (themeData && themeData["BasicTheme"] && themeData["BasicTheme"]["FontSize"]) {
    tableTitleDefautStyle["font-size"] = themeData["BasicTheme"]["FontSize"];
  }

  if (controlThemeData?.["TableTitleFontFamily"]) {
    tableTitleDefautStyle["font-family"] = controlThemeData["TableTitleFontFamily"];
  }
  if (controlThemeData?.["TableTitleFontSize"]) {
    tableTitleDefautStyle["font-size"] = controlThemeData["TableTitleFontSize"];
  }
  if (controlThemeData?.["TableTitleFontWeight"]) {
    if (controlThemeData?.["TableTitleFontWeight"] === "italic") {
      tableTitleDefautStyle["font-weight"] = 300;
      tableTitleDefautStyle["font-style"] = "italic";
    } else if (controlThemeData?.["TableTitleFontWeight"] === "Bold Italic") {
      tableTitleDefautStyle["font-weight"] = 600;
      tableTitleDefautStyle["font-style"] = "italic";
    } else if (controlThemeData?.["TableTitleFontWeight"] === "Bold") {
      tableTitleDefautStyle["font-weight"] = "bold";
      tableTitleDefautStyle["font-style"] = "normal";
    } else if (controlThemeData?.["TableTitleFontWeight"] === "normal") {
      tableTitleDefautStyle["font-weight"] = 300;
      tableTitleDefautStyle["font-style"] = "normal";
    }
  }

  if (controlThemeData?.["TableTitleFontColor"]) {
    tableTitleDefautStyle["color"] = `${controlThemeData["TableTitleFontColor"]} !important`;
  }

  var themeStyle = {
    tableTitleDefaultStyle: tableTitleDefautStyle
  };

  var jsonForTableTitle = { ...tableTitleDefautStyle };
  if (props.data && props.data.style) {
    if (props.data.style?.["ListviewTitleFontFamily"]) {
      jsonForTableTitle["ListviewTitleFontFamily"] = props.data.style["ListviewTitleFontFamily"];
    }
    if (props.data.style?.["ListviewTitleFontSize"]) {
      jsonForTableTitle["ListviewTitleFontSize"] = props.data.style["ListviewTitleFontSize"];
    }
    if (props.data.style?.["ListviewTitleFontWeight"]) {
      jsonForTableTitle["ListviewTitleFontWeight"] = props.data.style["ListviewTitleFontWeight"];
    }

    if (props.data.style["ListviewTitleFontStyle"]) {
      jsonForTableTitle["ListviewTitleFontStyle"] = props.data.style["ListviewTitleFontStyle"];
    }

    if (props.data.style?.["ListviewTitleFontColor"]) {
      jsonForTableTitle["ListviewTitleFontColor"] = `${props.data.style["ListviewTitleFontColor"]} !important`;
    }
  }

  const preparedStyle2 = CSSPreparation1(jsonForTableTitle);
  var styleSheet2 = StyleSheet.create({
    styling: preparedStyle2
  });

  var padding_left = props.data.style && props.data.style["SpacingLeft"];
  if (padding_left !== undefined && padding_left !== null && padding_left !== "") {
  } else {
    padding_left = "0px";
  }

  var padding_right = props.data.style && props.data.style["SpacingRight"];
  if (padding_right !== undefined && padding_right !== null && padding_right !== "") {
  } else {
    padding_right = "0px";
  }

  var padding_top = props.data.style && props.data.style["SpacingTop"];
  if (padding_top !== undefined && padding_top !== null && padding_top !== "") {
  } else {
    padding_top = "0px";
  }

  var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
  if (padding_bottom !== undefined && padding_bottom !== null && padding_bottom !== "") {
  } else {
    padding_bottom = "0px";
  }

  var inputStylePadding = {
    paddingLeft: padding_left,
    paddingRight: padding_right,
    paddingTop: padding_top,
    paddingBottom: padding_bottom
  };

  var inputStylePaddingListview = {
    position: "relative",
    border: "1px solid transparent",
    paddingLeft: padding_left,
    paddingRight: padding_right,
    paddingTop: padding_top,
    paddingBottom: padding_bottom
  };

  if (!formState2[`listviewRowDataCopy${props.data.id}`] && props.previewmode && props?.data?.rowData) {
    formState2[`listviewRowDataCopy${props.data.id}`] = JSON.parse(JSON.stringify(props?.data?.rowData));
  }

  const [render, setRender] = useState(false);
  const isListviewModalOpened = useRef("")

  function gridPopup(gridType, jsonForListview = {}) {
    if (gridType === "advanceListviewPopupInPreview") {
      dispatch(
        updateContextsStates({
          key: props.containerId,
          value: {
            advanceListviewStateJson: jsonForListview,
            currentAdvanceListviewStateJson: jsonForListview,
            openCanvasForPreview: "advanceListviewPopupInPreview",
            openCanvasForPreviewAdvance: "advanceListviewPopupInPreview"
          }
        })
        );
        setRender(!render);
      if (window.loading) {
        window.loading(false);
      }
    } else {
      isListviewModalOpened.current = jsonForListview?.id
      dispatch(
        updateContextsStates({
          key: props.containerId,
          value: { openCanvasForPreview: gridType, subFormListviewJson: jsonForListview },
          renderListView: true
        })
        );
    }
  }

  function handlePopupCloseListview() {
    if(isListviewModalOpened?.current === props?.data?.id){
      isListviewModalOpened.current = 0
    }
    let restrictedOverlayClose = false;
    if(window.allowRestrictedOverlay) {
      restrictedOverlayClose = window.allowRestrictedOverlay(); // if true is retruned then do not close the modal
    }

    if(!restrictedOverlayClose) {
      if ((advanceListviewStateJson === undefined || Object.keys(advanceListviewStateJson).length === 0) && openCanvasForPreviewAdvance !== "advanceListviewPopupInPreview") {
        dispatch(updateGridContexts({name: "openCanvasForPreview",  key: props.containerId, value: "", renderListView: true }));
      } else {
        dispatch(updateGridContexts({name: "openCanvasForPreview",  key: props.containerId, value: "advanceListviewPopupInPreview", renderListView: true }));
      }
    }
  }

  if (props.previewmode && data) {
    return (
      <div style={inputStylePadding}>
        {data.style["additionmethods"] === "Section above Listview" ? (
          <DataAdditionAboveSection data={data} previewmode={props.previewmode} containerId={props.containerId} />
        ) : (
          <></>
        )}
        {data && data.style && data.style["Visible"] === "true" ? (
          <>
            <Form.Group
              style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : { display: "block" }}
              className={`${styles2.tableStyle1} " spacingThemeControls " ${
                data.style["additionmethods"] === "Section above Listview" ? styles2.removeMarginTop : ""
              }`}
              control_id={userDefinedControlId + "_group"}
              key={data.id}
              elem_type="listview"
              id={data.id}
              frameindex={data.frameIndex}
              elementindex={data.elementIndex}
              sheetIndex={data.sheetIndex}
              sheetElementIndex={data.sheetElementIndex}
              sheetFrameElementindex={data.sheetFrameElementindex}
            >
              <ActionHeader
                type="listview"
                data={data}
                id={props.id}
                isSubformInPage={isSubformInPage}
                userDefinedControlId={userDefinedControlId}
                styleSheet2={styleSheet2}
                selectedRowsList={selectedRowsList}
                handleSelected={props.handleSelected}
                containerId={props.containerId}
                tableData={tableData}
                gridPopup={gridPopup}
                previewmode={props.previewmode}
                recordCount={recordCount}
                setRecordCount={setRecordCount}
                setTableData={setTableData}
                setLatestAddedRow={setLatestAddedRow}
                setSelectedRowsList={setSelectedRowsList}
                setLoading={setLoading}
                checkedKeys={checkedKeys}
                setCheckedKeys={setCheckedKeys}
                rowData={rowData}
                setRowData={setRowData}
                setFilterData={setFilterData}
                filterData={filterData}
                setSearchValue={setSearchValue}
                searchValue={searchValue}
                sortableColumns={sortableColumns}
                setSortableColumns={setSortableColumns}
                isSearched={isSearched}
                setIsSearched={setIsSearched}
              />

              <div className={styles2.basicTableParentDivP}>
                <DataTable
                  id={data.id}
                  tableData={tableData}
                  previewMode={props.previewmode}
                  loading={loading}
                  containerId={props.containerId}
                  setCheckedKeys={setCheckedKeys}
                  checkedKeys={checkedKeys}
                  gridPopup={gridPopup}
                  data={rowData}
                  setData={setRowData}
                  setFilterData={setFilterData}
                  filterData={filterData}
                  setTableData={setTableData}
                  recordCount={recordCount}
                  setRecordCount={setRecordCount}
                  sortableColumns={sortableColumns}
                  setSortableColumns={setSortableColumns}
                  isSearched={isSearched}
                  setIsSearched={setIsSearched}
                />
              </div>
            </Form.Group>

            {openCanvasForPreview === "showDataAdditonSection" && props?.data?.id === isListviewModalOpened?.current && (
              <ListviewPopUp
                {...props}
                open={openCanvasForPreview === "showDataAdditonSection"}
                onClose={handlePopupCloseListview}
                subFormListviewJson={subFormListviewJson}
                openCanvasForPreview={openCanvasForPreview}
                closeSubformOnPreview={handlePopupCloseListview}
              />
            )}
          </>
        ) : null}
      </div>
    );
  } else if (data) {
    return (
      <div
        onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, data.id, "n")}
        style={inputStylePaddingListview}
        onKeyDown={(e) => props.handleSelected(e)}
        tabIndex="1"
      >
        {data.style["additionmethods"] === "Section above Listview" ? (
          <DataAdditionAboveSection data={data} previewmode={props.previewmode} containerId={props.containerId} />
        ) : (
          <></>
        )}
        <Form.Group
          className={`${styles2.tableStyle1} " spacingThemeControls " ${
            data.style["additionmethods"] === "Section above Listview" ? styles2.removeMarginTop : ""
          }`}
          onKeyDown={(e) => props.handleSelected(e)}
          control_id={data.id}
          id={data.id}
          elem_type="listview"
          frameindex={data.frameIndex}
          elementindex={data.elementIndex}
          sheetIndex={data.sheetIndex}
          sheetElementIndex={data.sheetElementIndex}
          sheetFrameElementindex={data.sheetFrameElementindex}
        >
          <ActionHeader
            type="listview"
            data={data}
            id={props.id}
            isSubformInPage={isSubformInPage}
            userDefinedControlId={userDefinedControlId}
            styleSheet2={styleSheet2}
            selectedRowsList={selectedRowsList}
            handleSelected={props.handleSelected}
            containerId={props.containerId}
            tableData={data}
            previewmode={props.previewmode}
            recordCount={recordCount}
            setRecordCount={setRecordCount}
            setTableData={setTableData}
            setLatestAddedRow={setLatestAddedRow}
            setSelectedRowsList={setSelectedRowsList}
            setLoading={setLoading}
            checkedKeys={checkedKeys}
            setCheckedKeys={setCheckedKeys}
            rowData={rowData}
            setRowData={setRowData}
            setFilterData={setFilterData}
            filterData={filterData}
            setSearchValue={setSearchValue}
            searchValue={searchValue}
            sortableColumns={sortableColumns}
            setSortableColumns={setSortableColumns}
            isSearched={isSearched}
            setIsSearched={setIsSearched}
          />

          <div
            className={styles2.basicTableParentDivD}
            onClick={(e) => setSelectedControl(props.data)}
            control_id={props.data.id}
          >
            <DataTable
              id={data.id}
              tableData={props.data}
              previewMode={props.previewmode}
              loading={loading}
              containerId={props.containerId}
              setCheckedKeys={setCheckedKeys}
              checkedKeys={checkedKeys}
              data={rowData}
              setData={setRowData}
              setFilterData={setFilterData}
              filterData={filterData}
              setTableData={setTableData}
              recordCount={recordCount}
              setRecordCount={setRecordCount}
              sortableColumns={sortableColumns}
              setSortableColumns={setSortableColumns}
              isSearched={isSearched}
              setIsSearched={setIsSearched}
            />
          </div>
        </Form.Group>
        {controlSelected?.id === props?.id && (
          <ElemDeleteControl
            isSubformInPage={isSubformInPage}
            containerId={props.containerId}
            id={props.data.id}
            deleteControl={deleteControl}
            elemType={props.data.elem_type}
            setDragging={props.setDragging}
            handleReorderStart={props.handleReorderStart}
            handleReorderDrop={props.handleReorderDrop}
            dragId={props.dragId}
            dragType={"column"}
            data={props.data}
          />
        )}
        {window?.openQuickTools && open && (
          <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
        )}
      </div>
    );
  } else {
    return <></>;
  }
};

export default LISTVIEW;
