function formatDateValue(date, format) {
  const date1 = new Date(date);
  if (format === "4") {
    const month = date1.toLocaleString("default", { month: "long" });
    return `${date1.getDate()}/${month.substring(0, 3)}/${date1.getFullYear()}`;
  } else {
    let month = date1.toLocaleString("default", { month: "long" });
    return `${date1.getDate()}/${month.substring(
      0,
      3
    )}/${date1.getFullYear()} ${date1.getHours()}:${date1.getMinutes()}:00`;
  }
}
function reformatToDate(date, format) {
  let dateArr = date.split("/");
  const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
  if (format === "4") {
    let flag42 = 0;
    let q = isNaN(dateArr[0]);
    let r = isNaN(dateArr[2]);
    for (let i of months) {
      if (i === dateArr[1] && !q && 1 <= parseInt(dateArr[0]) <= 12 && !r && dateArr[2].length === 4) {
        flag42 = 1;
      }
    }
    if (flag42 === 1) {
      let formattedDate = `${dateArr[1]}/${dateArr[0]}/${dateArr[2]}`;
      return new Date(formattedDate);
    }
  } else {
    dateArr[2] = dateArr[2]?.replace(",", "");
    let dateArrNew = dateArr[2].split(" ");
    let dateArrNew2 = dateArrNew[1].split(":");
    let m = isNaN(dateArrNew2[0]);
    let n = isNaN(dateArrNew2[1]);
    let flag43 = 0;
    let q = isNaN(dateArr[0]);
    let r = isNaN(dateArrNew[0]);
    for (let i of months) {
      if (
        i === dateArr[1] &&
        !q &&
        1 <= parseInt(dateArr[0]) <= 12 &&
        !r &&
        dateArrNew[0].length === 4 &&
        !m &&
        !n &&
        1 <= parseInt(dateArrNew2[0]) <= 24 &&
        1 <= parseInt(dateArrNew2[1]) <= 60
      ) {
        flag43 = 1;
      }
    }

    if (flag43 === 1) {
      let formattedDate = `${dateArr[0]}/${dateArr[1]}/${dateArr[2]}`;
      return new Date(formattedDate);
    }
  }
}
const DateTimePickerCell = ({
  rowData,
  onChange,
  dataKey,
  colIndex,
  tableData,
  containerId,
  data,
  showAlertConfirmNotifications,
  isCellDisabled,
  columnValueAlignment,
  ...props
}) => {
  const classes = useStyles();
  const dateFormat = {
    5: "dd/MM/yyyy HH:mm",
    6: "MM/dd/yyyy HH:mm",
    7: "yyyy/MM/dd HH:mm",
    8: "dd/MMM/yyyy HH:mm"
  };

  const momentFormat = {
    5: "DD/MM/YYYY h:mm:ss",
    6: "MM/DD/YYYY h:mm:ss",
    7: "YYYY/MM/DD h:mm:ss",
    8: "DD/MMM/YYYY h:mm:ss"
  };
  const enableProperty = { root: classes.textfieldRoot };
  let textAlignment = "left";
  if (columnValueAlignment === "right") {
    textAlignment = "right";
  } else if (columnValueAlignment === "center") {
    textAlignment = "center";
  }
  console.log(
    "dateValue",
    moment(rowData[dataKey], momentFormat[data.style?.DateFormat] || "DD/MM/YYYY h:mm:ss").toDate()
  );
  if (rowData.isShowTotal) {
    // return <div style={showTotalCellStyle} title={rowData[dataKey]}>{rowData[dataKey]}</div>;
    return null;
  }
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils} key={dataKey}>
      <KeyboardDateTimePicker
        InputProps={{ classes: enableProperty, readOnly: true, "aria-label": "Table Date Picker" }}
        id={props.inputId}
        style={{
          display: "flex",
          flexDirection: "unset !important",
          height: "100%",
          textAlign: textAlignment
        }}
        clearable
        disabled={isCellDisabled}
        KeyboardButtonProps={{
          disabled: isCellDisabled,
          "aria-label": "Table Date Picker",
          style: isCellDisabled ? { display: "none" } : {}
        }}
        title={
          rowData[dataKey] === "" || rowData[dataKey] === "Invalid date"
            ? null
            : data.style?.DateFormat === "8"
            ? reformatToDate(rowData[dataKey], data.style?.DateFormat)
            : moment(rowData[dataKey], momentFormat[data.style?.DateFormat] || "DD/MM/YYYY h:mm:ss").toDate()
        }
        value={
          rowData[dataKey] === "" || rowData[dataKey] === "Invalid date"
            ? null
            : data.style?.DateFormat === "8"
            ? reformatToDate(rowData[dataKey], data.style?.DateFormat)
            : moment(rowData[dataKey], momentFormat[data.style?.DateFormat] || "DD/MM/YYYY h:mm:ss").toDate()
        }
        openTo="date"
        ampm={false}
        autoOk
        placeholder=""
        onChange={(date) => {
          let dateValue;
          if(data.style?.DateFormat === "8") {
            dateValue = formatDateValue(date, data?.style?.DateFormat)
          } else {
            moment.locale(window?.appformBuilder_configs?.selectedLanguage);
            dateValue = moment(date).format(momentFormat[data.style?.DateFormat] || "DD/MM/YYYY h:mm:ss");
          }
          onChange && onChange(rowData.id, dataKey, dateValue);

          onBlur(
            rowData.id,
            colIndex,
            dateValue,
            tableData.id,
            containerId,
            tableData,
            "2",
            data?.style?.DateFormat || "5",
            undefined,
            showAlertConfirmNotifications
          );
        }}
        format={dateFormat[data.style?.DateFormat] || "dd/MM/yyyy, HH:mm"}
      />
    </MuiPickersUtilsProvider>
  );
};
