import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import { Button } from "react-bootstrap";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import TooltipMui from "@material-ui/core/Tooltip";
import cloneDeep from "lodash-es/cloneDeep";
import { makeStyles } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import { TailSpin } from "react-loader-spinner";

import { useDispatch } from "../../../Redux";
import AddRecordIcon from "../../../Icons/AddRecordIcon";
import SvgDownloadIcon from "../../../Icons/DownloadIcon.svg";
import SvgSearchIcon from "../../../Icons/SearchIcon";
import styles2 from "../../css/COLUMN.module.css";
import axios from "../../../axios";
import { getGdToken, getWIToken } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import EditableLabel from "../../../ReusableComponents/EditableLabel/EditableLabel";
import CustomizedAlert from "../../../ReusableComponents/Alert/CustomizedAlert";
import TableLabel from "./TableLabel";
import {
  fetchFilterData,
  fetchFilterDataPortal,
  saveTableData,
  addRowAdvanceListview,
  saveTableDataInPortal
} from "../../../components/IFormDesignerLayout/RequestLogic";
import { formatTableData, getTotal } from "../DataTable";
import { updateGridContexts } from "../../../Redux/Actions/FormStateActions";
import { GetStateFromData } from "../../../components/CustomHooks/CustomHooks";

const useStyles = makeStyles(() => ({
  gridButtons: {
    "& .secondaryBtn": {
      color: (prop) =>
        prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
          ? `${prop.SecondaryButtonFontColor} !important`
          : "#0072c6 !important",
      backgroundColor: (prop) => "#fff !important",
      border: (prop) =>
        prop.SecondaryButtonBorderWidth && prop.SecondaryButtonBorderWidth !== "undefined" && prop.secondaryBorderColor
          ? `${prop?.SecondaryButtonBorderWidth || "1px"} solid ${prop.secondaryBorderColor}`
          : "1px solid #0072c6 !important",
      "&:focus": {
        boxShadow: (prop) => `0 0 0 0.2rem ${prop.primaryBackgroundColor}`,
        color: (prop) =>
          prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
            ? `${prop.SecondaryButtonFontColor} !important`
            : "#0072c6 !important",
        backgroundColor: (prop) => "#fff !important",
        border: (prop) =>
          prop.SecondaryButtonBorderWidth &&
          prop.SecondaryButtonBorderWidth !== "undefined" &&
          prop.secondaryBorderColor
            ? `${prop?.SecondaryButtonBorderWidth || "1px"} solid ${prop.secondaryBorderColor}`
            : "1px solid #0072c6 !important"
      },
      "& svg line": {
        stroke: (prop) =>
          prop.SecondaryButtonFontColor && prop.SecondaryButtonFontColor !== "undefined"
            ? `${prop.SecondaryButtonFontColor} !important`
            : "#0072c6 !important"
      }
    }
  },
  tooltip: {
    maxWidth: "160px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12
  },
  arrow: {
    "&:before": {
      border: "1px solid #70707075"
    },
    color: "white",
    fontSize: 15
  }
}));

const ActionHeader = ({
  type,
  data,
  id,
  isSubformInPage,
  styleSheet2,
  selectedRowsList,
  handleSelected,
  containerId,
  tableData,
  previewmode,
  setTableData,
  setLatestAddedRow,
  setSelectedRowsList,
  setLoading,
  checkedKeys,
  setCheckedKeys,
  gridPopup,
  rowData,
  setRowData,
  setFilterData,
  filterData,
  setSearchValue,
  setSortableColumns,
  isSearched,
  setIsSearched,
  searchValue
}) => {
  const translationStyles1 =
    window?.appformBuilder_configs?.selectedLanguage === "ar" ||
    window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

  const contexts = GetStateFromData(containerId);
  const gridContexts = GetStateFromData(containerId, "data2");

  const { advanceListviewStateJson, openCanvasForPreviewAdvance } = gridContexts.gridStates;

  const { t } = useTranslation(["styles", "translation"]);

  const {
    updateJson,
    builderMode,
    formState2,
    modeReadOrWrite,
    openingAsApplication,
    recordCount,
    setRecordCount,
    listviewColumnInfo
  } = contexts.contexts;

  const dispatch = useDispatch();
  const themeData = formState2.theme;
  const controlThemeData = themeData?.["ButtonStyle"];

  const addRecordButtonStyle = useStyles({
    primaryColor: `${controlThemeData["PrimaryButtonFontColor"] || "#fff"} !important`,
    primaryBackgroundColor:
      `${controlThemeData["PrimaryButtonBackgroundColor"]}  !important` || `var(--button_color)"} !important`,
    primaryBorderColor: `${controlThemeData["PrimaryButtonBorderRadius"] || "var(--button_color)"} !important`,
    secondaryColor: `${controlThemeData["SecondaryButtonFontColor"] || "var(--button_color)"} !important`,
    secondaryBorderColor: `${controlThemeData["SecondaryButtonBorderColor"] || "var(--button_color)"} !important`,
    SecondaryButtonBorderWidth: `${controlThemeData["SecondaryButtonBorderWidth"]}`,
    SecondaryButtonFontColor: `${controlThemeData["SecondaryButtonFontColor"]}`,
    SecondaryButtonFontFamily: `${controlThemeData["SecondaryButtonFontFamily"]}`
  });

  const { updatedComponentInFormState } = updateJson;

  const enableBatchStyle = { color: "black" };
  const disableBatchStyle = { color: "grey", "pointer-events": "none" };

  const [showAddRecordText, setShowAddRecordText] = useState(true);
  const [isBatchingEnable, setIsBatchingEnable] = useState(false);
  const [isBatchingLoading, setIsBatchingLoading] = useState(false);
  const [selectedColumnIndex, setSelectedColumnIndex] = useState("All Column");
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
  });
  const [editLabel, setEditLabel] = useState(false);
  const [defaultLabel, setDefaultLabel] = useState(data?.style?.Label);
  const [column_Width, setColumn_Width] = useState();
  const [fetchBtnStyle, setFetchBtnStyle] = useState({
    fetchNextStyle: enableBatchStyle,
    fetchPreviousStyle: enableBatchStyle
  });

  const resizeObserverRef = useRef(null);

  useLayoutEffect(() => {
    // Initialize ResizeObserver and attach it to the div element
    const targetElement = document.getElementById(`table_SearchAddRecord_id_${data.id}`); // Replace with your selector

    if (!targetElement) {
      console.error("Element not found");
      return;
    }

    resizeObserverRef.current = new ResizeObserver((entries) => {
      for (const entry of entries) {
        const { width, height, top, bottom } = entry.contentRect;
        console.log("Width2:", width);
        console.log("Height2:", height);
        if (previewmode) setColumn_Width(width);
        console.log("Top2:", top);
        console.log("Bottom2:", bottom);
      }
    });

    // Start observing the div element
    if (previewmode) {
      resizeObserverRef.current.observe(targetElement);
    }

    // Cleanup function
    return () => {
      if (resizeObserverRef.current) {
        resizeObserverRef.current.unobserve(targetElement);
      }
    };
  }, []);

  function onChangeHandler(event) {
    setSearchValue(event.target.value);
  }

  function onChangeSearchValue(event, isSearchIconClicked) {
    if (event.key === "Enter" || isSearchIconClicked) {
      if (!isSearched) setIsSearched(true);
      handleSearch(event, event.target.value);
    }
  }

  function handleSelectedColumn(event) {
    setSelectedColumnIndex(event.target.value);
  }

  useEffect(() => {
    const tablSearchAddRecorddiv = document.getElementById(`table_SearchAddRecord_id_${data.id}`);
    if (tablSearchAddRecorddiv && column_Width < 400) {
      setShowAddRecordText(false);
    } else {
      setShowAddRecordText(true);
    }
  }, [column_Width]);

  useEffect(() => {
    if (data?.style?.Label) {
      setDefaultLabel(data.style["Label"]);
    }
  }, [data]);

  useEffect(() => {
    checkFetchBtnStyle(data, "search", true);
  }, []);

  useEffect(() => {
    if (isBatchingEnable) {
      setSelectedColumnIndex((prev) => (prev === "All Column" ? "0" : prev));
    }
  }, [isBatchingEnable]);

  function generateColumn(id, ColumnName, value) {
    return { field: id, headerName: ColumnName, height: 36, value };
  }

  let isEnable = true;
  if (previewmode && modeReadOrWrite === "r") {
    data.style.Enable = "false";
    isEnable = false;
  }

  let columns = [];
  if (data?.columnList) {
    if (builderMode === "application" || formState2.isBatchingEnabled === "N" || !formState2.isBatchingEnabled) {
      columns.push(generateColumn(t(`translation:All Column`), "All Column", "All Column"));
    }

    if (openCanvasForPreviewAdvance !== "advanceListviewPopupInPreview") {
      data.columnList.map((obj) => {
        if (obj?.style?.["VisibleOnTable"] === "true" && obj?.style?.["EnableSearch"] === "true") {
          columns.push(generateColumn(obj.style.ColumnName, obj.style.ColumnName, obj.columnIndex));
        }
      });
    }
  }

  function showServerError(isGlobalError) {
    if (window.loading && isGlobalError) {
      window.loading(false);
    } else {
      setLoading(false);
    }
    setAlertData({
      open: true,
      showConfirm: "showNotification",
      alertType: "error",
      alertContentKey: "There is some issue at server end."
    });
  }

  function addRecord() {
    if (type === "table") addRecordTable();
    else if (type === "listview") addRecordListview();
    else if (type === "advancelistview") addRecordInAdvanceListview();
  }

  function addRecordInAdvanceListview() {
    if (window.loading) {
      window.loading(true, undefined, undefined, false);
    }
    let result = true;
    if (window.tableOperation) {
      result = window.tableOperation(tableData.id, "AddRow");
    }
    window.currentRowIndexListview = recordCount;
    if (result) {
      const jsonToSend = {
        rowIndex: "-1",
        controlId: data.id,
        modifyFlag: "no",
        witoken: getWIToken(containerId),
        gdtoken: getGdToken(containerId)
      };
      addRowAdvanceListview(jsonToSend)
        .then((response) => {
          if (response?.data) {
            data.listviewFrameLayoutJson = cloneDeep(response.data?.ele_JSON?.listviewFrameLayoutJson || null);
            gridPopup("advanceListviewPopupInPreview", { ...data, modalType: "A" }, true, -1);
          } else {
            showServerError(true);
          }
        })
        .catch(() => {
          showServerError(true);
        });
    }
    document.activeElement.blur();
  }

  function addRecordListview() {
    let result = true;
    let openOverlayResult = true;
    listviewColumnInfo.clear();
    if (window.tableOperation) {
      result = window.tableOperation(tableData.id, "AddRow");
    }
    if (window.openOverlay) {
      openOverlayResult = window.openOverlay(tableData.id);
    }
    if (openOverlayResult && result) {
      gridPopup("showDataAdditonSection", { ...data, modalType: "A" }, true);
      dispatch(updateGridContexts({ name: "listViewAddRecordFrom", key: containerId, value: true }));
      window.currentRowIndexListview = recordCount;
    }
    document.activeElement.blur();
  }

  function addRecordTable() {
    let result = true;

    if (window.tableOperation) {
      result = window.tableOperation(tableData.id, "AddRow");
    }

    if (data?.columnList && result) {
      const jsonToSend = {
        witoken: getWIToken(containerId), // getWIToken()
        controlId: data.id,
        gdtoken: getGdToken(containerId) //getGdToken()
      };
      const remainingURL = "/tableAction/addRow";
      setLoading(true);
      axios
        .post(remainingURL, jsonToSend, { withCredentials: true })
        .then((response) => {
          if (response?.data?.status === "0") {
            window.setFormMoified(true);

            setRecordCount(id, true);

            if (!Object.keys(tableData).includes("rowData")) {
              tableData["rowData"] = [];
            }
            let tableRowData = tableData.rowData;
            tableRowData.push({ ...response.data.valueJson });
            if (advanceListviewStateJson) {
              updatedComponentInFormState(tableData.id, tableData, true);
            } else {
              setTableData((prev) => {
                return { ...prev };
              });
              updatedComponentInFormState(tableData.id, tableData);
            }

            setLoading(false);
            setLatestAddedRow(() => tableData.rowData?.length + 1);
            if (window.addRowPostHook) {
              window.addRowPostHook(tableData.id);
            }
          }
        })
        .catch((e) => {
          console.log(e);
          showServerError();
        });
    }
  }

  function deleteRecord(event, key) {
    if (key) {
      if (event !== "Enter") {
        return;
      }
    }
    if (!checkedKeys?.length) {
      return;
    }

    let result = true;
    if (window.tableOperation) {
      result = window.tableOperation(data.id, "DeleteRow");
    }

    if (data?.rowData && result) {
      const jsonToSend = {
        witoken: getWIToken(containerId),
        gdtoken: getGdToken(containerId),
        controlId: tableData.id,
        rowIndices: checkedKeys.join(",") + ""
      };
      const remainingURL = "/tableAction/deleteRow";
      if (checkedKeys && checkedKeys?.length > 0) {
        axios.post(remainingURL, jsonToSend, { withCredentials: true }).then((response) => {
          if (response?.data?.status === "0") {
            const result = tableData.rowData.filter((row, index) => !checkedKeys.includes(index));
            tableData.rowData = [...result];
            const rowDataResult = rowData.filter((row, index) => !checkedKeys.includes(index));
            setRowData([...rowDataResult]);
            setTableData({ ...tableData });
            setCheckedKeys([]);
            setRecordCount(id, true);

            updatedComponentInFormState(tableData?.id, tableData);
            if (window.deleteRowPostHook) {
              window.deleteRowPostHook(data.id, checkedKeys.join(",") + "");
            }
          }
          setSelectedRowsList([]);
        });
      }
    }
  }

  function fetchPreviousData(event) {
    if (event) {
      if (event !== "Enter") {
        return;
      }
    }

    setIsBatchingLoading(true);

    let jsonToSend = {
      gdtoken: contexts.gdtoken,
      witoken: contexts.witoken,
      controlId: data.id,
      searchValue: searchValue,
      searchEnabled: "",
      colIndex: "",
      batchType: "D",
      operation: "N"
    };

    if (builderMode === "application") {
      fetchFilterDataPortal(jsonToSend)
        .then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
            data.rowData = [...rowData];

            setTableData((prev) => ({ ...prev, rowData }));
          }

          checkFetchBtnStyle(tableData, "previous");
          if (window.nextPreviousBatchHook) {
            window.nextPreviousBatchHook(data.id);
          }
          updatedComponentInFormState(data.id, data);
          setIsBatchingLoading(false);
        })
        .catch(() => {
          setIsBatchingLoading(false);
        });
    } else {
      const saveJsonToSend = {
        gdtoken: contexts.gdtoken,
        witoken: contexts.witoken,
        isSaveFlag: true
      };

      saveTableData(saveJsonToSend);

      fetchFilterData(jsonToSend)
        .then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
            data.rowData = [...rowData];

            setTableData((prev) => ({ ...prev, rowData }));
          }

          checkFetchBtnStyle(tableData, "previous");
          if (window.nextPreviousBatchHook) {
            window.nextPreviousBatchHook(data.id);
          }
          updatedComponentInFormState(data.id, data);
          setIsBatchingLoading(false);
        })
        .catch(() => {
          setIsBatchingLoading(false);
        });
    }
  }

  function fetchNextData(event) {
    if (event) {
      if (event !== "Enter") {
        return;
      }
    }
    setIsBatchingLoading(true);

    let jsonToSend = {
      gdtoken: contexts.gdtoken,
      witoken: contexts.witoken,
      controlId: data.id,
      searchValue: searchValue,
      searchEnabled: "",
      colIndex: "",
      batchType: "A",
      operation: "N"
    };

    if (builderMode === "application") {
      fetchFilterDataPortal(jsonToSend)
        .then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
            data.rowData = [...rowData];

            setTableData((prev) => ({ ...prev, rowData }));
          }

          checkFetchBtnStyle(tableData, "next");
          if (window.nextPreviousBatchHook) {
            window.nextPreviousBatchHook(data.id);
          }
          updatedComponentInFormState(data.id, data);
          setIsBatchingLoading(false);
        })
        .catch(() => {
          setIsBatchingLoading(false);
        });
    } else {
      const saveJsonToSend = {
        gdtoken: contexts.gdtoken,
        witoken: contexts.witoken,
        isSaveFlag: true
      };

      saveTableData(saveJsonToSend);

      fetchFilterData(jsonToSend)
        .then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
            data.rowData = [...rowData];

            setTableData((prev) => ({ ...prev, rowData }));
          }

          checkFetchBtnStyle(tableData, "next");
          if (window.nextPreviousBatchHook) {
            window.nextPreviousBatchHook(data.id);
          }
          updatedComponentInFormState(data.id, data);
          setIsBatchingLoading(false);
        })
        .catch(() => {
          setIsBatchingLoading(false);
        });
    }
  }

  const handleDownload = async (cb_data, errorCallback = null) => {
    try {
      if (cb_data !== "Enter") {
        return;
      }
      const xmlhttp = new XMLHttpRequest();
      xmlhttp.open(
        "GET",
        `${window.apb_backend_app_context}/tableAction/downloadXls/${getWIToken(containerId)}/${getGdToken(
          containerId
        )}/${data.id}`,
        true
      ); //WdAudit URL changes

      xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
      xmlhttp.setRequestHeader("Authorization", axios?.defaults?.headers?.common?.["Authorization"]);
      xmlhttp.responseType = "blob";
      xmlhttp.send(JSON.stringify(cb_data));

      xmlhttp.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          let fileName = data.id;
          const blob = this.response;

          if (window.navigator.msSaveOrOpenBlob) {
            window.navigator.msSaveBlob(blob, fileName);
          } else {
            const downloadLink = window.document.createElement("a");
            const contentTypeHeader = xmlhttp.getResponseHeader("Content-Type");

            if (contentTypeHeader.includes("application/json") && errorCallback) {
              blob.text().then((res) => {
                let parsedRes = JSON.parse(res);
                if (parsedRes?.status?.maincode === "-1000" || parsedRes?.status?.maincode === "11") {
                }
                errorCallback(JSON.parse(res));
              });
              return false;
            }

            downloadLink.href = window.URL.createObjectURL(new Blob([blob], { type: contentTypeHeader }));
            fileName = decodeURIComponent(fileName);

            downloadLink.download = fileName;
            document.body.appendChild(downloadLink);
            downloadLink.click();
            document.body.removeChild(downloadLink);
          }
        }
      };
    } catch (err) {
      errorCallback(err);
    }
  };

  function disabledColumns(columns) {
    return columns.filter((column) => column.style.EnableSearch === "false" || column.style.VisibleOnTable === "false");
  }

  function getColumnName(columns) {
    if (type === "advancelistview") {
      return columns.map((item) => item.style.complexFieldName);
    } else {
      return columns.map((item) => item.style.ColumnName);
    }
  }

  function globalSearch(searchValue) {
    let result = [];
    let disableSearchList = disabledColumns(tableData.columnList);
    let finalList = getColumnName(disableSearchList);

    for (let row of rowData) {
      for (let i in row) {
        if (finalList.includes(i)) continue;
        let itemValue = row[i];
        if (
          !row.isShowTotal &&
          itemValue &&
          typeof itemValue === "string" &&
          itemValue.toLowerCase().includes(searchValue.toLowerCase())
        ) {
          result.push(row);
          break;
        }
      }
    }
    return result;
  }

  function columnwiseSearch(searchValue, selectedColIndex) {
    let result = [];
    let colName = data?.columnList?.[selectedColIndex]?.style?.ColumnName || "";

    if (data?.elem_type?.toLowerCase() === "advancelistview") {
      colName = data?.columnList?.[selectedColIndex]?.style?.complexFieldName || colName || "";
    }

    for (let row of rowData) {
      let itemValue = row[colName];
      if (!row.isShowTotal && (itemValue || itemValue === false)) {
        itemValue = itemValue.toString();
        if (itemValue.toLowerCase().includes(searchValue.toLowerCase())) {
          result.push(row);
        }
      }
    }
    return result;
  }

  function handleSearchLogic(event, searchedValue) {
    setRecordCount(id, false);
    let isGlobal = true;
    let selectedColIndex = undefined;

    if (selectedColumnIndex === "All Column") {
      selectedColIndex = "-1";
    } else {
      selectedColIndex = parseInt(selectedColumnIndex);
      isGlobal = false;
    }

    let jsonToSend = {
      gdtoken: contexts.gdtoken,
      witoken: contexts.witoken,
      controlId: data.id,
      searchValue: event.key === "Enter" ? searchedValue : searchValue,
      searchEnabled: "true",
      colIndex: selectedColIndex,
      batchType: "A",
      operation: "S"
    };

    setLoading(true);

    if (isGlobal || builderMode === "application") {
      let searchText = event.key === "Enter" ? searchedValue : searchValue;

      if (searchText) {
        let result = [];
        if (selectedColIndex === "-1") {
          result = globalSearch(searchText);
        } else {
          result = columnwiseSearch(searchText, selectedColIndex);
        }
        setFilterData(result);
        setLoading(false);
      } else {
        if (builderMode === "application") {
          fetchFilterDataPortal({ ...jsonToSend, colIndex: "-1" }).then((response) => {
            if (response.data.rowData) {
              let rowData = response.data.rowData;
              tableData.rowData = [...rowData];
            }
            updatedComponentInFormState(data.id, data);
            setLoading(false);
          });
        } else {
          try {
            fetchFilterData({ ...jsonToSend, colIndex: "-1" }).then((response) => {
              if (response.data.rowData) {
                let rowData = response.data.rowData;
                tableData.rowData = [...rowData];
              }
              updatedComponentInFormState(data.id, data);
              let result = formatTableData(tableData, filterData);
              result = handleShowTotal(result);
              setFilterData(result);
              checkFetchBtnStyle(tableData, "search");
              setLoading(false);
            });
          } catch (err) {
            showServerError();
          }
        }
      }
    } else {
      if (builderMode === "application") {
        fetchFilterDataPortal(jsonToSend).then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
          }
          updatedComponentInFormState(data.id, data);
          setLoading(false);
        });
      } else {
        fetchFilterData(jsonToSend).then((response) => {
          if (response.data.rowData) {
            let rowData = response.data.rowData;
            tableData.rowData = [...rowData];
          }
          updatedComponentInFormState(data.id, data);
          checkFetchBtnStyle(tableData, "search");
          setLoading(false);
        });
      }
    }

    // resetting sorting on empty search
    let searchText = event.key === "Enter" ? searchedValue : searchValue;
    if (!searchText) {
      setSortableColumns((prev) => {
        const updatedState = {};
        Object.keys(prev || {}).forEach((item) => (updatedState[item] = ""));
        return updatedState;
      });
    }
  }

  const handleShowTotal = (dataToSum, isChange, key) => {
    const nextData = [...(dataToSum || [])];

    if (
      nextData.length &&
      nextData[nextData.length - 1]?.isShowTotal &&
      (tableData?.columnList?.some((item) => item?.style?.ShowTotalListview === "true") ||
        tableData?.style?.ShowTotal === "true")
    ) {
      if (isChange) {
        nextData[nextData.length - 1][key] = getTotal(nextData, key, tableData) || "";
      } else {
        Object.keys(dataToSum[0]).forEach((item) => {
          nextData[nextData.length - 1][item] = getTotal(nextData, item, tableData) || "";
        });
      }
    }
    return nextData;
  };

  function handleSearch(event, searchedValue) {
    if (previewmode && data?.rowData) {
      const controlRecordObj = recordCount.get(id) || {};

      if (controlRecordObj.isUpdated) {
        formState2[`${type}RowDataCopy${data.id}`] = cloneDeep(data?.rowData);
        let saveJsonToSend = {};
        if (builderMode === "application") {
          saveJsonToSend = {
            gdtoken: contexts.gdtoken,
            witoken: contexts.witoken,
            applicationId: openingAsApplication.applicationId,
            applicationName: openingAsApplication.applicationName,
            submitApplication: "N",
            stepIndex: formState2?.isStep?.parentStep || "0",
            stepSubIndex: formState2?.isStep?.parentSubStep || "0",
            isBackClicked: false
          };
        } else {
          saveJsonToSend = {
            gdtoken: contexts.gdtoken,
            witoken: contexts.witoken,
            isSaveFlag: true
          };
        }

        setAlertData({
          open: true,
          showConfirm: "showconformationDialogBox",
          alertContentKey: "confirmSaveTableData",
          alertTitleKey: "confirmSaveTitle",
          Button1Key: "CancelButton",
          Button2Key: "Confirm",
          saveTable: true,
          saveTableJSON: saveJsonToSend,
          saveTableData: builderMode === "application" ? saveTableDataInPortal : saveTableData,
          searchEvent: event,
          searchValue: searchedValue,
          searchTableFunc: handleSearchLogic
        });
      } else {
        let isGlobal = true;
        let selectedColIndex = undefined;

        if (selectedColumnIndex === "All Column") {
          selectedColIndex = "-1";
        } else {
          selectedColIndex = parseInt(selectedColumnIndex);
          isGlobal = false;
        }

        let jsonToSend = {
          gdtoken: contexts.gdtoken,
          witoken: contexts.witoken,
          controlId: data.id,
          searchValue: event.key === "Enter" ? searchedValue : searchValue,
          searchEnabled: "true",
          colIndex: selectedColIndex,
          batchType: "A",
          operation: "S" //Sort
        };

        setLoading(true);

        if (isGlobal || builderMode === "application") {
          let searchText = event.key === "Enter" ? searchedValue : searchValue;

          if (searchText) {
            let result = [];
            if (selectedColIndex === "-1") {
              result = globalSearch(searchText);
            } else {
              result = columnwiseSearch(searchText, selectedColIndex);
            }
            setFilterData(result);
            setLoading(false);
          } else {
            if (builderMode === "application") {
              fetchFilterDataPortal({ ...jsonToSend, colIndex: "0" }).then((response) => {
                if (response.data.rowData) {
                  let rowData = response.data.rowData;
                  tableData.rowData = [...rowData];
                }

                if (tableData?.style) {
                  tableData.style["updateFormState"] = new Date();
                }

                updatedComponentInFormState(tableData.id, tableData);
                setLoading(false);
              });
            } else {
              try {
                fetchFilterData({ ...jsonToSend, colIndex: "-1" }).then((response) => {
                  if (response.data.rowData) {
                    let rowData = response.data.rowData;
                    setTableData((prev) => {
                      return { ...prev, rowData: rowData };
                    });
                  }
                  let result = formatTableData(tableData, filterData);
                  result = handleShowTotal(result);

                  checkFetchBtnStyle(response?.data, "search");
                  setFilterData(result);
                  setLoading(false);
                });
              } catch (err) {
                showServerError();
              }
            }
          }
        } else {
          if (builderMode === "application") {
            fetchFilterDataPortal(jsonToSend).then((response) => {
              if (response.data.rowData) {
                let rowData = response.data.rowData;
                tableData.rowData = [...rowData];
              }

              if (tableData?.style) {
                tableData.style["updateFormState"] = new Date();
              }

              updatedComponentInFormState(tableData.id, tableData);
              setLoading(false);
            });
          } else {
            fetchFilterData(jsonToSend)
              .then((response) => {
                if (response.data.rowData) {
                  let rowData = response.data.rowData;
                  setTableData((prev) => {
                    return { ...prev, rowData: rowData };
                  });
                }
                setLoading(false);
                checkFetchBtnStyle(response?.data, "search");
              })
              .catch((error) => {
                showServerError();
              });
          }
        }

        // resetting sorting on empty search
        let searchText = event.key === "Enter" ? searchedValue : searchValue;
        if (!searchText) {
          setSortableColumns((prev) => {
            const updatedState = {};
            Object.keys(prev || {}).forEach((item) => (updatedState[item] = ""));
            return updatedState;
          });
        }
      }
    }
  }

  const checkFetchBtnStyle = (data = { rowData: [] }, from, fromUseEffect) => {
    if (data?.rowData?.length) {
      const isPortal = builderMode === "application";
      let RetrievedCount = data.rowData[0]["RetrievedCount"] || 0;
      let TotalCount = data.rowData[0]["TotalCount"] || 0;

      if (
        (RetrievedCount === 0 && TotalCount === 0) ||
        isPortal ||
        formState2.isBatchingEnabled === "N" ||
        !formState2.isBatchingEnabled
      ) {
        setIsBatchingEnable(false);
        setFetchBtnStyle({
          fetchPreviousStyle: disableBatchStyle,
          fetchNextStyle: disableBatchStyle
        });
        return;
      } else {
        setIsBatchingEnable(true);
      }

      if (from === "next") {
        if (TotalCount === RetrievedCount) {
          setFetchBtnStyle({
            fetchPreviousStyle: enableBatchStyle,
            fetchNextStyle: disableBatchStyle
          });
        } else {
          setFetchBtnStyle({
            fetchPreviousStyle: enableBatchStyle,
            fetchNextStyle: enableBatchStyle
          });
        }
      } else if (from === "previous") {
        if (TotalCount === RetrievedCount || fromUseEffect) {
          setFetchBtnStyle({
            fetchPreviousStyle: disableBatchStyle,
            fetchNextStyle: enableBatchStyle
          });
        } else {
          setFetchBtnStyle({
            fetchPreviousStyle: enableBatchStyle,
            fetchNextStyle: enableBatchStyle
          });
        }
      } else if (from === "search") {
        if (TotalCount === RetrievedCount) {
          setFetchBtnStyle({
            fetchPreviousStyle: disableBatchStyle,
            fetchNextStyle: disableBatchStyle
          });
        } else {
          setFetchBtnStyle({
            fetchPreviousStyle: disableBatchStyle,
            fetchNextStyle: enableBatchStyle
          });
        }
      }
    } else {
      setFetchBtnStyle({
        fetchPreviousStyle: disableBatchStyle,
        fetchNextStyle: disableBatchStyle
      });
    }
  };

  const bringEditableLabel = () => {
    setEditLabel(true);
  };

  const setLabelHandler = (recievedLabel) => {
    try {
      if (data.style && (data.style.Label || data.id)) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  if (!formState2[`${type}RowDataCopy${data.id}`] && previewmode && rowData) {
    formState2[`${type}RowDataCopy${data.id}`] = cloneDeep(rowData);
  }

  const marginStyle = translationStyles1
    ? { marginRight: "8px", marginLeft: "unset" }
    : { marginLeft: "8px", marginRight: "unset" };

  return (
    <div type={type} elem_type={type} control_id={data.id} className={styles2.tableSearchAndSelectDiv}>
      <div
        className={styles2.tableFormLabelDiv}
        control_id={data.id}
        elem_type={type}
        onClick={isSubformInPage || (previewmode && data) ? () => {} : (e) => handleSelected(e, data.id, "n")}
      >
        {previewmode && data ? (
          <TableLabel data={data} control_id={data.id} styles2={styles2} styleSheet2={styleSheet2} />
        ) : (
          <EditableLabel
            defaultLabel={
              <TableLabel
                data={data}
                control_id={id}
                styles2={styles2}
                styleSheet2={styleSheet2}
                onDoubleClick={bringEditableLabel}
              />
            }
            editlabel={editLabel}
            default_value={defaultLabel}
            setLabelHandler={(recievedLabel) => setLabelHandler(recievedLabel)}
          />
        )}
      </div>

      {previewmode && data ? (
        <>
          <div
            id={`table_SearchAddRecord_id_${data.id}`}
            className={[styles2.tableSearchAndSelectInnerDiv, addRecordButtonStyle.gridButtons].join(" ")}
          >
            {data?.style?.["Searching"] === "true" ? (
              <>
                <select
                  className={[
                    styles2.tableDropdownDiv,
                    ...[translationStyles1 ? [styles2.tableDropdownDiv__ml] : [styles2.tableDropdownDiv__mr]]
                  ].join(" ")}
                  onChange={handleSelectedColumn}
                  disabled={!rowData?.length && !isSearched}
                  title={rowData?.length ? "" : t(`translation:Please_enter_record`)}
                  id={`${data?.id}_searchDropdown`}
                >
                  {columns.map((object) => (
                    <option name={object.field} value={object.value} key={object.value}>
                      {object.field}
                    </option>
                  ))}
                </select>

                <input
                  aria-label={"Enter Record"}
                  type="text"
                  style={{
                    pointerEvents: isEnable === false ? "none" : "initial"
                  }}
                  className={[styles2.tableSearchDiv, styles2.tableSearch].join(" ")}
                  placeholder={!rowData?.length && !isSearched ? "" : t(`translation:Search`)}
                  disabled={!rowData?.length && !isSearched}
                  onChange={onChangeHandler}
                  onKeyDown={(e) => onChangeSearchValue(e, false)}
                  id={`${data?.id}_searchInput`}
                />
                <span
                  className={styles2.tableSearchIcon}
                  style={{
                    ...(!rowData?.length && !isSearched ? {} : { cursor: "default" }),
                    ...(translationStyles1 ? { marginLeft: "unset", marginRight: "-20px" } : {})
                  }}
                  onClick={!rowData?.length && !isSearched ? () => {} : (e) => onChangeSearchValue(e, true)}
                  id={`${data?.id}_searchIcon`}
                >
                  <SvgSearchIcon />
                </span>
              </>
            ) : null}

            {data?.style?.["AllowDownload"] === "true" ? (
              <TooltipMui
                title={t(`translation:Download`)}
                classes={{
                  tooltip: addRecordButtonStyle.tooltip,
                  arrow: addRecordButtonStyle.arrow
                }}
                placement={"bottom"}
                arrow
              >
                <div
                  style={{
                    pointerEvents: isEnable ? (rowData.length ? "initial" : "none") : "none",
                    cursor: isEnable ? (rowData.length ? "pointer" : "not-allowed") : "not-allowed",
                    backgroundColor: isEnable ? (rowData.length ? "" : "#eee") : "#eee",
                    ...marginStyle
                  }}
                  onKeyDown={(event) => handleDownload(event.key)}
                  onClick={() => handleDownload("Enter")}
                  className={styles2.tableRowDeleteIcon}
                  tabIndex={rowData.length ? "0" : "-1"}
                  title={rowData?.length ? "" : t(`translation:Please_enter_record`)}
                  id={`${data?.id}_download_record`}
                >
                  {/* <SvgDownloadIcon /> */}
                  <img alt="Download Icon" src={SvgDownloadIcon} style={{ width: "17px" }} />
                </div>
              </TooltipMui>
            ) : null}

            {(data?.style?.["HideDelete"] === "false" || data?.style?.["HideDelete"] === false) &&
            data?.style?.["Enable"] === "true" &&
            checkedKeys &&
            checkedKeys.length === 0 ? (
              <TooltipMui
                title={t(`translation:Delete`)}
                classes={{
                  tooltip: addRecordButtonStyle.tooltip,
                  arrow: addRecordButtonStyle.arrow
                }}
                placement={"bottom"}
                arrow
              >
                <div
                  className={styles2.tableRowDeleteIcon}
                  style={{
                    pointerEvents: "none",
                    backgroundColor: "#eee",
                    ...marginStyle
                  }}
                  tabIndex={checkedKeys.length === 0 ? "-1" : "0"}
                  onKeyDown={(event) => deleteRecord(event.key, "onKey")}
                  onClick={() => deleteRecord("Enter")}
                  title={checkedKeys?.length ? "" : t(`translation:Please_enter_record`)}
                  id={`${data?.id}_delete_record`}
                >
                  <img alt="Delete Icon" src={require("../../../Icons/TableRowDelete.svg").default} />
                </div>
              </TooltipMui>
            ) : (data?.style?.["HideDelete"] === "false" || data?.style?.["HideDelete"] === false) &&
              data?.style?.["Enable"] === "true" ? (
              <TooltipMui
                title={t(`translation:Delete`)}
                classes={{
                  tooltip: addRecordButtonStyle.tooltip,
                  arrow: addRecordButtonStyle.arrow
                }}
                placement={"bottom"}
                arrow
              >
                <div
                  className={styles2.tableRowDeleteIcon}
                  tabIndex="0"
                  onKeyDown={(event) => deleteRecord(event.key, "onKey")}
                  onClick={() => deleteRecord("Enter")}
                  style={{ ...marginStyle }}
                  id={`${data?.id}_delete_record`}
                >
                  <img
                    alt="Delete Icon"
                    src={require("../../../Icons/TableRowDelete.svg").default}
                    title={t(`translation:Delete Row`)}
                    style={{
                      cursor: "pointer",
                      pointerEvents: isEnable === false ? "none" : "initial"
                    }}
                  />
                </div>
              </TooltipMui>
            ) : null}

            {(data?.style?.["HideAdd"] === "false" || data?.style?.["HideAdd"] === false) &&
            data?.style?.["Enable"] === "true" ? (
              <>
                {data.style["additionmethods"] !== "Section above Listview" ? (
                  <Button
                    disableFocusRipple
                    disableRipple
                    tabIndex={0}
                    size="sm"
                    id={`${data.id}_add_record_btn`}
                    className={`${styles2.addRecordButton} ${
                      translationStyles1 ? styles2.addRecordButton_mr : styles2.addRecordButton_ml
                    } button primary secondaryBtn formBuilder-MuiButton-outlined`}
                    frameindex={data.frameIndex}
                    elementindex={data.elementIndex}
                    style={{
                      pointerEvents: isEnable === false || !getGdToken() ? "none" : "initial"
                    }}
                    onClick={addRecord}
                    title={t(`translation:Add_record`)}
                    disabled={getGdToken() ? false : true}
                  >
                    <span className={styles2.plusIconSpan}>
                      <AddRecordIcon />
                    </span>

                    {showAddRecordText && t(`translation:Add_record`)}
                    {/* Add New Task  */}
                  </Button>
                ) : null}

                {builderMode !== "application" && isBatchingEnable && (
                  <div className={styles2.batchParent}>
                    {isBatchingLoading ? (
                      <TailSpin height="28" width="28" color="#000" ariaLabel="tail-spin-loading" />
                    ) : (
                      <>
                        <TooltipMui
                          title={t(`translation:Back`)}
                          classes={{
                            tooltip: addRecordButtonStyle.tooltip,
                            arrow: addRecordButtonStyle.arrow
                          }}
                          placement={"bottom"}
                          arrow
                        >
                          <div
                            className={styles2.batchTypeD}
                            onClick={() => fetchPreviousData("Enter")}
                            style={fetchBtnStyle.fetchPreviousStyle}
                            tabIndex="0"
                            onKeyDown={(event) => fetchPreviousData(event.key)}
                            id={`${data.id}_previousBatch`}
                          >
                            <ChevronLeftIcon />
                          </div>
                        </TooltipMui>

                        <TooltipMui
                          title={t(`translation:Next`)}
                          classes={{
                            tooltip: addRecordButtonStyle.tooltip,
                            arrow: addRecordButtonStyle.arrow
                          }}
                          placement={"bottom"}
                          arrow
                        >
                          <div
                            className={styles2.batchTypeA}
                            onClick={() => fetchNextData("Enter")}
                            style={fetchBtnStyle.fetchNextStyle}
                            tabIndex="0"
                            onKeyDown={(event) => fetchNextData(event.key)}
                            id={`${data.id}_nextBatch`}
                          >
                            <ChevronRightIcon />
                          </div>
                        </TooltipMui>
                      </>
                    )}
                  </div>
                )}
              </>
            ) : null}
          </div>

          {alertData.open === true && <CustomizedAlert alertData={alertData} />}
        </>
      ) : (
        <div className={[styles2.tableSearchAndSelectInnerDiv].join(" ")} control_id={id} style={{ cursor: "default" }}>
          {data?.style?.["SearchBar"] === "true" ? (
            <>
              <select
                className={[
                  styles2.tableDropdownDiv,
                  ...[translationStyles1 ? [styles2.tableDropdownDiv__ml] : [styles2.tableDropdownDiv__mr]]
                ].join(" ")}
                control_id={id}
              >
                {columns.map((object, index) => {
                  return (
                    <option control_id={id} name={object.field} value={index} key={object.field}>
                      {object.field}
                    </option>
                  );
                })}
              </select>
              <input
                aria-label={"Search Value"}
                type="text"
                className={[styles2.tableSearchDiv, styles2.tableSearch].join(" ")}
                placeholder={t(`translation:Search`)}
              />
              <span
                className={styles2.tableSearchIcon}
                style={translationStyles1 ? { marginLeft: "unset", marginRight: "-20px" } : null}
                control_id={id}
              >
                <SvgSearchIcon />
              </span>
            </>
          ) : null}

          <TooltipMui
            title={t(`translation:Download`)}
            classes={{
              tooltip: addRecordButtonStyle.tooltip,
              arrow: addRecordButtonStyle.arrow
            }}
            placement={"bottom"}
            arrow
          >
            <div
              className={styles2.tableRowDeleteIcon}
              control_id={id}
              tabIndex="0"
              style={{ pointerEvents: "none", cursor: "unset", backgroundColor: "#eee", ...marginStyle }}
            >
              <img alt="Download Icon" src={SvgDownloadIcon} style={{ width: "17px" }} />
            </div>
          </TooltipMui>

          {selectedRowsList && selectedRowsList.length === 0 ? (
            <TooltipMui
              title={t(`translation:Delete`)}
              classes={{
                tooltip: addRecordButtonStyle.tooltip,
                arrow: addRecordButtonStyle.arrow
              }}
              placement={"bottom"}
              arrow
            >
              <div
                className={styles2.tableRowDeleteIcon}
                control_id={id}
                tabIndex="0"
                style={{ pointerEvents: "none", cursor: "unset", backgroundColor: "#eee", ...marginStyle }}
              >
                <img alt="Delete Icon" src={require("../../../Icons/TableRowDelete.svg").default} control_id={id} />{" "}
              </div>
            </TooltipMui>
          ) : (
            <TooltipMui
              title={t(`translation:Delete`)}
              classes={{
                tooltip: addRecordButtonStyle.tooltip,
                arrow: addRecordButtonStyle.arrow
              }}
              placement={"bottom"}
              arrow
            >
              <div
                className={styles2.tableRowDeleteIcon}
                control_id={id}
                tabIndex="0"
                style={{ pointerEvents: "none", cursor: "unset", backgroundColor: "#eee", ...marginStyle }}
              >
                <img alt="Delete Icon" control_id={id} src={require("../../../Icons/TableRowDelete.svg").default} />
              </div>
            </TooltipMui>
          )}
        </div>
      )}
    </div>
  );
};

export default ActionHeader;
